import { Component, OnInit, Input } from '@angular/core';
import { Vinho } from '../models/vinho.model';
import { VinhoService } from '../services/vinho.service';
import { Atributo } from '../models/atributo.model';

@Component({
  selector: 'app-vinho',
  templateUrl: './vinho.component.html',
  styleUrls: ['./vinho.component.css'],
  providers: [VinhoService]
})
export class VinhoComponent implements OnInit {

  vinho : Vinho
  vinhos : Vinho[]

  atr : Atributo = new Atributo()

  constructor(private vinhoService : VinhoService) { 
    this.vinho = new Vinho()
    
  }

  ngOnInit() {
    this.getVinhos()
  }

  public getVinhos(){
    this.vinhoService.getVinhos().subscribe(
      vinhos => this.vinhos = vinhos,
      error => alert(error),
      
    )
  }

  public addVinho() : void {
    console.log(this.vinho)
    alert(this.vinho.nomeVinho);
    this.vinhoService.addVinho(this.vinho).subscribe(
      data => data,
      error => alert(error),
      () => this.getVinhos()
      
    )
    
  }

  public loadVinho(vinho : Vinho){
    this.vinho = new Vinho()
    this.vinho.id = vinho.id
    this.vinho.nomeVinho = this.vinho.nomeVinho
    this.vinho.atributosVinho = vinho.atributosVinho
  } 

  public updateVinho(vinho : Vinho){
    this.vinhoService.updateVinho(this.vinho).subscribe(
      data => data,
      error => alert(error),
      () => this.getVinhos()
    )
    
    
  }
  
  deleteUser(vinho : Vinho){
    this.vinhoService.deleteVinho(vinho).subscribe(
      data => data,
      error => alert(error),
     () => this.getVinhos() 
    )
  }

addAtributo(){
  this.vinho.atributosVinho.push(new Atributo());
  
}

}
