import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import {map, retry} from 'rxjs/operators'
import { Vinho } from '../models/vinho.model';

@Injectable()
export class VinhoService {
  constructor(private _http: Http) { }

  getVinhos(): Observable<Vinho[]> {
    return this._http.
      get('http://env-3218961.jelasticlw.com.br/rest/vinhorest')
      .pipe(map(this.extractData));
      
  }

  private extractData(res: Response) {
    return res.json();
  }

  addVinho(vinho : Vinho): Observable<string> {
    const json = JSON.stringify(vinho);
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this._http.
      post('http://env-3218961.jelasticlw.com.br/rest/vinhorest',
      json, options).pipe(map(res => res.json()));
  }

  updateVinho(vinho: Vinho): Observable<string> {
    const json = JSON.stringify(vinho);
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this._http.
      put('http://env-3218961.jelasticlw.com.br/rest/vinhorest',
      json, options).pipe(map(res => res.json()));
  }

  deleteVinho(vinho: Vinho): Observable<string> {
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    
    return this._http.
        delete('http://env-3218961.jelasticlw.com.br/rest/vinhorest/'+vinho.id ,
         options).pipe(map(res => res.json()));
  }

}