import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import {Usuario} from '../models/usuario.model'
import {map, retry} from 'rxjs/operators'

@Injectable()
export class UsuarioService {
  constructor(private _http: Http) { }

  getUsers(): Observable<Usuario[]> {
    return this._http.
      get('http://env-3218961.jelasticlw.com.br/rest/usuariorest')
      .pipe(map(this.extractData));
      
  }

  private extractData(res: Response) {
    return res.json();
  }

  addUser(usuario: Usuario): Observable<string> {
    const json = JSON.stringify(usuario);
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this._http.
      post('http://env-3218961.jelasticlw.com.br/rest/usuariorest',
      json, options).pipe(map(res => res.json()));
  }

  updateUser(usuario: Usuario): Observable<string> {
    const json = JSON.stringify(usuario);
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this._http.
      put('http://env-3218961.jelasticlw.com.br/rest/usuariorest',
      json, options).pipe(map(res => res.json()));
  }

  deleteUser(usuario: Usuario): Observable<string> {
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    
    return this._http.
        delete('http://env-3218961.jelasticlw.com.br/rest/usuariorest/' + usuario.id ,
         options).pipe(map(res => res.json()));
  }

}