import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import {map, retry} from 'rxjs/operators'
import { Atributo } from '../models/atributo.model';

@Injectable()
export class AtributoService {
  constructor(private _http: Http) { }

  getAtributos(): Observable<Atributo[]> {
    return this._http.
      get('http://env-3218961.jelasticlw.com.br/rest/atributorest')
      .pipe(map(this.extractData));
      
  }

  private extractData(res: Response) {
    return res.json();
  }

  addAtributo(atributo: Atributo): Observable<string> {
    const json = JSON.stringify(atributo);
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this._http.
      post('http://env-3218961.jelasticlw.com.br/rest/atributorest',
      json, options).pipe(map(res => res.json()));
  }

  updateAtributo(atributo: Atributo): Observable<string> {
    const json = JSON.stringify(atributo);
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    return this._http.
      put('http://env-3218961.jelasticlw.com.br/rest/atributorest',
      json, options).pipe(map(res => res.json()));
  }

  deleteAtributo(atributo: Atributo): Observable<string> {
    const headers = new Headers({ 'Content-Type': 'application/json' });
    const options = new RequestOptions({ headers: headers });
    
    return this._http.
        delete('http://env-3218961.jelasticlw.com.br/rest/atributorest/' + atributo.id ,
         options).pipe(map(res => res.json()));
  }

}