import { Component, OnInit, Input, Output } from '@angular/core';
import { Atributo } from '../models/atributo.model';
import { AtributoService } from '../services/atributo.service';
import { EventEmitter } from 'protractor';

@Component({
  selector: 'app-atributo',
  templateUrl: './atributo.component.html',
  styleUrls: ['./atributo.component.css'],
  providers: [ AtributoService ]
})
export class AtributoComponent implements OnInit {


  atributos : Atributo[]
  atributo : Atributo

  constructor(private atribitoService : AtributoService) {
    this.atributo = new Atributo()
    this.getAtributos()
   }

  ngOnInit() {
    this.getAtributos()
  }

  public getAtributos(){
    this.atribitoService.getAtributos().subscribe(
      usuarios => this.atributos = this.atributos,
      error => alert(error),
      () => console.log('terminou')
    )
  }

  public addAtributo() : void {
    alert(this.atributo.nomeAtributo);
    this.atribitoService.addAtributo(this.atributo).subscribe(
      data => data,
      error => alert(error),
      () => this.getAtributos()
    )
  }

  public loadAtributo(atributo : Atributo){
    this.atributo = new Atributo()
    this.atributo.id = atributo.id
    this.atributo.nomeAtributo = atributo.nomeAtributo
    
  } 

  public updateAtributo(atr : Atributo){
    this.atribitoService.updateAtributo(this.atributo).subscribe(
      data => data,
      error => alert(error),
      () => this.getAtributos()
    )
    
    
  }
  
  deleteAtributo(atr : Atributo){
    this.atribitoService.deleteAtributo(atr).subscribe(
      data => data,
      error => alert(error),
     () => this.getAtributos() 
    )
  }

  
}
