import { Component, OnInit } from '@angular/core';
import { Usuario } from '../models/usuario.model';
import { UsuarioService } from '../services/usuario.service';

@Component({
  selector: 'app-usuario',
  templateUrl: './usuario.component.html',
  styleUrls: ['./usuario.component.css'],
  providers : [UsuarioService]
})
export class UsuarioComponent implements OnInit {

  usuarios : Usuario[]
  usuario : Usuario
  
  constructor(private usuarioService : UsuarioService) { 
    this.usuario = new Usuario()
    this.getUsuarios()
  }

  ngOnInit() {
    

  }

  public getUsuarios(){
    this.usuarioService.getUsers().subscribe(
      usuarios => this.usuarios = usuarios,
      error => alert(error),
      () => console.log('terminou')
    )
  }

  public addUsuario() : void {
    alert(this.usuario.nomeUsuario);
    this.usuarioService.addUser(this.usuario).subscribe(
      data => data,
      error => alert(error),
      () => this.getUsuarios()

    )
  }

  public loadUser(usuario : Usuario){
    this.usuario = new Usuario()
    this.usuario.id = usuario.id
    this.usuario.nomeUsuario = usuario.nomeUsuario
    this.usuario.emailUsuario = usuario.emailUsuario
  } 

  public updateUsuario(usuario : Usuario){
    this.usuarioService.updateUser(this.usuario).subscribe(
      data => data,
      error => alert(error),
      () => this.getUsuarios()
    )
    
    
  }
  
  deleteUser(user : Usuario){
    this.usuarioService.deleteUser(user).subscribe(
      data => data,
      error => alert(error),
     () => this.getUsuarios() 
    )
  }

}

